/* Container */
.container {
  width: 100%;
  margin: 0;
  padding: 24px;
  background: white;
  min-height: 100vh;
  overflow-x: hidden; 
}

.header {
  margin-bottom: 32px;
}

.headerContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 24px 32px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
}

.headerText {
  flex: 1;
}

.pageTitle {
  font-size: 32px;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 8px;
}

.pageSubtitle {
  font-size: 16px;
  color: #64748b;
  margin: 0;
}

.userInfo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.userAvatar {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

/* Main Grid Layout */
.mainGrid {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 16px;
  width: 100%;
}

.leftColumn {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.rightColumn {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

/* Ensure consistent spacing for all cards */
.welcomeCard,
.quickActionsCard,
.performanceOverview,
.programDistribution {
  margin: 0 0 16px 0;
  width: 100%;
}

/* Specific adjustments for quick actions and performance overview */
.quickActionsCard {
  margin-bottom: 8px;  /* Reduced from 16px to 8px */
}

.performanceOverview {
  margin-top: 0;
}

/* Performance Overview */
.performanceOverview {
  background: white;
  padding: 32px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
}

.performanceOverview > .sectionHeader {
  margin: 0 0 24px 0;
  max-width: 100%;
}

.metricsGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.metricCard {
  background: white;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.metricCard:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.metricIcon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.metricContent {
  flex: 1;
}

.metricValue {
  display: block;
  font-size: 24px;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 4px;
}

.metricLabel {
  font-size: 14px;
  color: #64748b;
  font-weight: 500;
}

/* Section Headers */
.sectionHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 32px 0 16px;
}

.sectionTitle {
  font-size: 24px;
  font-weight: 700;
  color: #1e293b;
  margin: 0;
}

.quickActionsContainer {
  background: white;
  padding: 24px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
  margin-bottom: 16px;
}

.quickActionsContainer > .sectionHeader {
  margin: 0 0 24px 0;
  max-width: 100%;
}

.quickActionsContainer > .cardGrid {
  max-width: 100%;
}

/* Enhanced Navigation Cards */
.cardWrapper {
  display: flex;
}
.cardGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  width: 100%;
}

.cardGrid .navCard {
  background: white;
  border-radius: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  overflow: hidden;
}

.cardGrid .navCard:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.cardContent {
  padding: 20px;
  cursor: pointer;
  display: flex;
  align-items: flex-start;
  gap: 16px;
  position: relative;
}

.cardIconWrapper {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.cardTextContent {
  flex: 1;
}

.cardTitle {
  margin: 0 0 8px;
  font-size: 16px;
  font-weight: 600;
  color: #1e293b;
}

.cardDescription {
  margin: 0;
  font-size: 14px;
  color: #64748b;
  line-height: 1.5;
}

.expandIcon {
  position: absolute;
  top: 20px;
  right: 20px;
  color: #94a3b8;
}

.expandableContent {
  background: #f8fafc;
}

.linkGrid {
  padding: 16px 20px;
}

.linkItem {
  display: block;
  padding: 12px 16px;
  text-decoration: none;
  color: #3b82f6;
  font-weight: 500;
  border-radius: 12px;
  transition: background-color 0.2s ease;
  font-size: 14px;
  margin-bottom: 4px;
}

.linkItem:hover {
  background-color: #eff6ff;
}

/* Recent Activity */
.activityContainer {
  background: white;
  border-radius: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  height: fit-content;
  padding: 24px;
  margin-bottom: 16px;
}

/* Program Distribution */
.distributionContainer {
  background: white;
  border-radius: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  padding: 24px;
  margin-top: 0;
}

.activityHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
}

.activityTitleWrapper {
  display: flex;
  align-items: center;
  gap: 12px;
}

.activityIconWrapper {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.activityTitle {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #1e293b;
}

.activityContent {
  padding: 0 24px 24px;
  max-height: 600px;
  overflow-y: auto;
}

.activityLoader {
  display: flex;
  justify-content: center;
  padding: 40px;
}

.activityList {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.activityItem {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 16px;
  background: #f8fafc;
  border-radius: 20px;
  position: relative;
  transition: background-color 0.2s ease;
}

.activityItem:hover {
  background: #f1f5f9;
}

.activityItemIcon {
  width: 36px;
  height: 36px;
  background: #e2e8f0;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #64748b;
  flex-shrink: 0;
}

.activityItemContent {
  flex: 1;
}

.activityItemHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
}

.activityType {
  font-size: 14px;
  font-weight: 600;
  color: #334155;
}

.activityTime {
  font-size: 12px;
  color: #94a3b8;
}

.activityDescription {
  margin: 0;
  font-size: 13px;
  color: #64748b;
  line-height: 1.4;
}

.activityStatus {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  right: 16px;
}

.activityStatus.success {
  background-color: #10b981;
}

.activityStatus.active {
  background-color: #f59e0b;
}

.activityStatus.error {
  background-color: #ef4444;
}

.emptyState {
  text-align: center;
  padding: 40px 20px;
  color: #94a3b8;
}

/* Charts */

.distributionContainer {
  background: white;
  padding: 32px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  border: 1px solid #e2e8f0;
  margin-bottom: 32px;
}

.distributionContainer > .sectionHeader {
  margin: 0 0 24px 0;
  max-width: 100%;
}

.distributionContainer > .chartsContainer {
  max-width: 100%;
}
.chartsContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 24px;
  max-width: 80%;
}

.chartCard,
.tableCard {
  background: white;
  border-radius: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
}

.chartCard {
  overflow: hidden;
}

.chartHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
}

.chartTitle {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #1e293b;
}

.chartContent {
  padding: 0 24px 24px;
  overflow: hidden !important;
  min-height: 400px;
  border-radius: 0 0 20px 20px !important;
}

.chartContent > div,
.chartContent > div > div,
.chartContent svg {
  border-radius: 0 0 20px 20px !important;
  overflow: hidden !important;
}

/* Chart Tooltip Styling */
.recharts-tooltip-wrapper .recharts-default-tooltip {
  border-radius: 12px !important;
  border: 1px solid #e2e8f0 !important;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1) !important;
}

.recharts-tooltip-content {
  border-radius: 12px !important;
  background: white !important;
  border: 1px solid #e2e8f0 !important;
}

.tableContent {
  padding: 0 24px 24px;
  border-radius: 0 0 16px 16px;
}

/* Program Table */
.programTable {
  width: 100%;
  border-collapse: collapse;
}

.tableHeader {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr;
  gap: 16px;
  padding: 16px 0;
  border-bottom: 2px solid #e2e8f0;
  font-weight: 600;
  color: #334155;
}

.tableHeaderCell {
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tableRow {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr;
  gap: 16px;
  padding: 16px 0;
  border-bottom: 1px solid #e2e8f0;
  transition: background-color 0.2s ease;
}

.tableRow:first-child {
  border-radius: 8px 8px 0 0;
}

.tableRow:last-child {
  border-radius: 0 0 8px 8px;
  border-bottom: none;
}

.tableRow:hover {
  background-color: #f8fafc;
}

.tableCell {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #334155;
}

.programType {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.programType.withRegistration {
  background-color: #dbeafe;
  color: #1d4ed8;
}

.programType.withoutRegistration {
  background-color: #dcfce7;
  color: #166534;
}

.viewMoreButton {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 500;
  background: #f1f5f9;
  border: 1px solid #e2e8f0;
  color: #475569;
  transition: all 0.2s ease;
}

.viewMoreButton:hover {
  background: #e2e8f0;
  border-color: #cbd5e1;
}

/* Loader */
.loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 400px;
  background: white;
  border-radius: 16px;
  margin: 40px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
}

/* Chart Modal */
.chartModal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
}

.chartModalContent {
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 95vw;
  max-height: 95vh;
  width: 1200px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.chartModalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 32px;
  border-bottom: 1px solid #e2e8f0;
  background: #f8fafc;
}

.chartModalTitle {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  color: #1e293b;
}

.chartModalBody {
  padding: 32px;
  flex: 1;
  overflow: auto;
  min-height: 650px;
}


